"use client"

import { useState, useRef, useEffect } from "react"
import { PageLayout } from "@/components/page-layout"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { Badge } from "@/components/ui/badge"
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { Bot, Send, User, MapPin, Droplet, Heart, AlertTriangle, ExternalLink } from "lucide-react"
import { mockReports } from "@/lib/mock-data"

interface Message {
  id: string
  sender: "user" | "bot"
  content: string
  timestamp: Date
  suggestions?: string[]
  mapData?: {
    reportId: string
    location: string
    lat: number
    lng: number
  }
}

export default function ChatbotPage() {
  const [messages, setMessages] = useState<Message[]>([
    {
      id: "1",
      sender: "bot",
      content: "Hello! I'm UDAN AI Assistant with real-time access to disaster reports across Uttarakhand. I can check road conditions, water safety, health alerts, and help you make informed safety decisions. Ask me anything!",
      timestamp: new Date(),
      suggestions: [
        "Is it safe to go to school today?",
        "Should I take the Tehri Dam road?",
        "Is the water safe to drink in Joshimath?",
        "Any health alerts in my area?",
      ],
    },
  ])
  const [inputMessage, setInputMessage] = useState("")
  const [isTyping, setIsTyping] = useState(false)
  const [showMapDialog, setShowMapDialog] = useState(false)
  const [selectedMapData, setSelectedMapData] = useState<{
    reportId: string
    location: string
    lat: number
    lng: number
    title: string
  } | null>(null)
  const messagesEndRef = useRef<HTMLDivElement>(null)

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" })
  }

  useEffect(() => {
    scrollToBottom()
  }, [messages])

  const handleSendMessage = async () => {
    if (!inputMessage.trim()) return

    const userMessage: Message = {
      id: Date.now().toString(),
      sender: "user",
      content: inputMessage,
      timestamp: new Date(),
    }

    setMessages([...messages, userMessage])
    setInputMessage("")
    setIsTyping(true)

    try {
      // Call the Gemini AI API
      const response = await fetch("/api/emergency-advice", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ description: inputMessage }),
      })

      const data = await response.json()

      const botResponse: Message = {
        id: (Date.now() + 1).toString(),
        sender: "bot",
        content: data.advice || "I'm having trouble connecting right now. Please try again.",
        timestamp: new Date(),
        suggestions: [
          "Tell me more",
          "Show me on map",
          "What else should I know?",
        ],
      }
      setMessages((prev) => [...prev, botResponse])
    } catch (error) {
      console.error("Error getting AI response:", error)
      const errorResponse: Message = {
        id: (Date.now() + 1).toString(),
        sender: "bot",
        content: "I'm having trouble connecting right now. For emergencies, please call 108 (ambulance) or 1070 (disaster helpline).",
        timestamp: new Date(),
      }
      setMessages((prev) => [...prev, errorResponse])
    } finally {
      setIsTyping(false)
    }
  }

  const handleSuggestionClick = (suggestion: string) => {
    setInputMessage(suggestion)
  }

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === "Enter" && !e.shiftKey) {
      e.preventDefault()
      handleSendMessage()
    }
  }

  return (
    <PageLayout>
      <div className="mx-auto max-w-5xl px-4 py-8 sm:px-6 lg:px-8">
        {/* Header */}
        <div className="mb-6">
          <div className="mb-4 flex items-center gap-3">
            <div className="flex h-12 w-12 items-center justify-center rounded-xl bg-gradient-to-br from-blue-500 to-purple-600 text-white">
              <Bot className="h-6 w-6" />
            </div>
            <div>
              <h1 className="text-3xl font-bold">UDAN AI Assistant</h1>
              <p className="text-muted-foreground">Get instant guidance on safety, routes, and emergency resources</p>
            </div>
          </div>
        </div>

        {/* Chat Interface */}
        <Card className="flex h-[600px] flex-col">
          <CardHeader className="border-b">
            <div className="flex items-center gap-3">
              <Avatar className="h-10 w-10">
                <AvatarFallback className="bg-gradient-to-br from-blue-500 to-purple-600 text-white">
                  <Bot className="h-5 w-5" />
                </AvatarFallback>
              </Avatar>
              <div>
                <CardTitle className="text-lg">UDAN AI</CardTitle>
                <CardDescription className="flex items-center gap-1">
                  <span className="h-2 w-2 rounded-full bg-green-500" />
                  Online • Ready to help
                </CardDescription>
              </div>
            </div>
          </CardHeader>

          {/* Messages Area */}
          <CardContent className="flex-1 overflow-y-auto p-4">
            <div className="space-y-4">
              {messages.map((message) => (
                <div key={message.id} className={`flex ${message.sender === "user" ? "justify-end" : "justify-start"}`}>
                  <div className={`flex max-w-[80%] gap-3 ${message.sender === "user" ? "flex-row-reverse" : ""}`}>
                    <Avatar className="h-8 w-8">
                      <AvatarFallback className={message.sender === "bot" ? "bg-gradient-to-br from-blue-500 to-purple-600 text-white" : "bg-primary text-primary-foreground"}>
                        {message.sender === "bot" ? <Bot className="h-4 w-4" /> : <User className="h-4 w-4" />}
                      </AvatarFallback>
                    </Avatar>
                    <div>
                      <div className={`rounded-2xl px-4 py-2 ${
                        message.sender === "user" 
                          ? "bg-primary text-primary-foreground" 
                          : "bg-muted"
                      }`}>
                        <p className="text-sm">{message.content}</p>
                      </div>
                      <p className="mt-1 px-2 text-xs text-muted-foreground">
                        {message.timestamp.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}
                      </p>
                      {message.suggestions && (
                        <div className="mt-2 flex flex-wrap gap-2">
                          {message.suggestions.map((suggestion, idx) => (
                            <Button
                              key={idx}
                              variant="outline"
                              size="sm"
                              onClick={() => handleSuggestionClick(suggestion)}
                              className="text-xs"
                            >
                              {suggestion}
                            </Button>
                          ))}
                        </div>
                      )}
                    </div>
                  </div>
                </div>
              ))}

              {isTyping && (
                <div className="flex justify-start">
                  <div className="flex gap-3">
                    <Avatar className="h-8 w-8">
                      <AvatarFallback className="bg-gradient-to-br from-blue-500 to-purple-600 text-white">
                        <Bot className="h-4 w-4" />
                      </AvatarFallback>
                    </Avatar>
                    <div className="rounded-2xl bg-muted px-4 py-2">
                      <div className="flex gap-1">
                        <span className="h-2 w-2 animate-bounce rounded-full bg-muted-foreground [animation-delay:-0.3s]" />
                        <span className="h-2 w-2 animate-bounce rounded-full bg-muted-foreground [animation-delay:-0.15s]" />
                        <span className="h-2 w-2 animate-bounce rounded-full bg-muted-foreground" />
                      </div>
                    </div>
                  </div>
                </div>
              )}
              <div ref={messagesEndRef} />
            </div>
          </CardContent>

          {/* Input Area */}
          <div className="border-t p-4">
            <div className="flex gap-2">
              <Input
                placeholder="Ask me anything about safety, routes, health..."
                value={inputMessage}
                onChange={(e) => setInputMessage(e.target.value)}
                onKeyPress={handleKeyPress}
                className="flex-1"
              />
              <Button onClick={handleSendMessage} disabled={!inputMessage.trim()}>
                <Send className="h-4 w-4" />
              </Button>
            </div>
            <div className="mt-2 flex flex-wrap gap-2">
              <Badge variant="outline" className="cursor-pointer hover:bg-muted" onClick={() => handleSuggestionClick("Road conditions near me")}>
                <AlertTriangle className="mr-1 h-3 w-3" />
                Road Safety
              </Badge>
              <Badge variant="outline" className="cursor-pointer hover:bg-muted" onClick={() => handleSuggestionClick("Where can I find clean water?")}>
                <Droplet className="mr-1 h-3 w-3" />
                Water
              </Badge>
              <Badge variant="outline" className="cursor-pointer hover:bg-muted" onClick={() => handleSuggestionClick("First aid tips")}>
                <Heart className="mr-1 h-3 w-3" />
                Health
              </Badge>
              <Badge variant="outline" className="cursor-pointer hover:bg-muted" onClick={() => handleSuggestionClick("Nearest hospital")}>
                <MapPin className="mr-1 h-3 w-3" />
                Location
              </Badge>
            </div>
          </div>
        </Card>
      </div>
    </PageLayout>
  )
}
